$main: #10182D;
$text: #11172D;
$grey: #D4D5D9;
$grey-bg: #ecedef;
$grey-bg-2: #DCDDE1;

@mixin bg {
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
}

%divider {
  &::after {
    content: "";
    position: absolute;
    height: 40px;
    width: 50vw;
    background-image: url(../images/icons/line.svg);
    @include bg;
    top: -64px;
    z-index: 3;
  }
}

%h2 {
  font-weight: bold;
  font-size: 110px;
  line-height: 134px;
  text-transform: uppercase;
  color: $grey;
  position: relative;
}

